// Nav menu
//
// A "nav menu" is secondary, hierarchical navigation which can be displayed to the
// side or below the main content.
//
// Markup: nav-menu.twig
//
// Style guide: navigation.nav-menu

// Default link styling
.page.menu-block-wrapper.menu-name-main-menu.menu-block-2 {
  @extend .au-link-list;
  padding: 0;
}

// Local navigation
.au-body .sidebar {
  #block-menu-block-2,
  #block-book-navigation {

    @extend %health-localnav;

    display: none;
    @include AU-media(sm) {
      display: block;
    }

    // Drupal block title as link helper styles
    &__title {
      font-size: 18px;
      margin-bottom: 0;
    }
  }
}

// When the block title is a link
.au-body .sidebar .block-menu-block--title-link {
  .block__title {
    margin: 0;
    a {
      font-size: 18px;
      @extend %health-localnav__link;
      border-top: $health-borderize--thin;
    }
  }
  // The indent needs to adjust as the parent item is now a link
  .menu {
    a {
      @include AU-space('padding-left', 1unit);
    }
    ul {
      a {
        @include AU-space('padding-left', 1.5unit);
      }
      ul a {
        @include AU-space('padding-left', 2unit);
      }
    }
  }
}

// Mobile toggle.
.au-body .localnav__mobile-toggle.health-accordion__title {
  @extend %health-localnav__mobile-toggle;
  a {
    border-bottom: 0;
  }
}